<template>
  <div>
    <h2>
      I've been programming since
      <span class="bold big">{{ startDate }}</span>
      <br />and developing solutions professionally since
      <span class="bold big">{{ professionalDate }}</span>.
    </h2>
    <h2>
      That's
      <span class="bold big italic">{{ yearsOfExperience }}+ years of insight</span>
      that you can leverage to make your projects come to fruition.
    </h2>
  </div>
</template>

<script>
export default {
  name: "Time",
  data: function() {
    return {
      startDate: 2009,
      professionalDate: 2015
    };
  },
  computed: {
    yearsOfExperience: function() {
      return new Date().getFullYear() - this.startDate;
    }
  }
};
</script>

